/**
 * [wxRegister Api]
 * @param  appid
 * @param  appsecret
 * @param  {Function} callback [ready]
 */
export function wxRegister () {
  let p = new Promise(function (resolve, reject) {
    // console.log('window.location.href.split(\'#\')[0]',window.location.href.split('#')[0]);
    let params = {
      url: encodeURIComponent(window.location.href.split('#')[0]),
      appid: localStorage.appid,
      secret: localStorage.serect
    }
    console.log('',params);
    /**  store.api.getWeixinConfig  */
    axios.post('/api/signature', params).then(res => {
      console.log('',res);
      if (res.data.message === 'OK') {
        let params = {
          // beta: true, // wx.invokejsapi
          debug: false, // 
          appId: localStorage.appid, // APP_ID
          timestamp: parseInt(res.data.data.timestamp), // 
          nonceStr: res.data.data.nonceStr, // 
          signature: res.data.data.signature, // 1
          jsApiList: [ // JSJS2
            'onMenuShareTimeline', // 
            'onMenuShareAppMessage', // 
            'onMenuShareQQ', // qq
            'onMenuShareQZone', // qq
            'onMenuShareWeibo', // 
            'closeWindow', // 
            'scanQRCode', // 
            'onHistoryBack', // 
            'openDefaultBrowser' // 
          ]
        };
        console.log('',params);
        // console.log(params);
        wx.config(params);
        wx.ready((res) => {
          console.log('', res);
          resolve(res);
          // return res;
        });
        wx.error(function (res) {
          console.log('' + JSON.stringify(res));
        });
      } else {
        console.log(res.message)
      }
      // resolve(res);
    }).catch(error => {
      console.log(error);
    })
  });
  return p;
}

