/**
 *  
 * @params meetingID-string meetingID
 * @params name-string name
 * @params meetingNo-string numericId
 * @params meetingUrl-string joinUrl
 * @params userName -string 
 * @params token -string token
 * @params userId -string userId
 * */
export function startMeeting (params) {
  switch (localStorage.platformType) {
    // 
    case 'wxwork': {
      return callDefaultBrowserWeixin(params)
    }
    // 
    default: {
      return callStartMeeting(params)
    }
  }
}

/**
 * 
 * @params meetingID-string meetingID
 * @params name-string name
 * @params password-string password
 * @params meetingNo-string numericId
 * @params meetingUrl-string joinUrl
 * */
export function joinMeeting (params) {
  switch (localStorage.platformType) {
    // 
    case 'wxwork': {
      return callDefaultBrowserWeixin(params)
    }
    // 
    default: {
      return callJoinMeeting(params)
    }
  }
}

/**
 * share url
 * @params platformType-Number// 1:  2:  3:  27:
 * @params contentType(Number)// 0:  1:  2:
 * @params title(string)  // 
 * @params description(string)  // 
 * @params imageUrlThumb(string)  // 
 * @params webPageUrl(string) // 
 * */
export function share (platformType, contentType, title, text, description, imageUrlShare, imageUrlThumb, webPageUrl) {
  let params = {
    'platformType': platformType,
    'contentType': contentType, // 0:  1:  2:
    'title': title,
    'text': text,
    'description': description,
    'imageUrlShare': imageUrlShare,
    'imageUrlThumb': imageUrlThumb,
    'webPageUrl': webPageUrl
  };
  switch (localStorage.platformType) {
    // 
    case 'wxwork': {
      return shareWeixin(params)
    }
    // 
    default: {
      return callShare(params)
    }
  }
}

export function initHomePage () {
  switch (localStorage.platformType) {
    // 
    case 'wxwork': {
      initHomePageWeixin()
      break
    }
    default: {
    }
  }
}
export default {
  registerPlatform,
  registerPlatform1,
  judgePlatform,
  getAuthCode,
  setHeaderBar,
  initHeaderView,
  watchGoback,
  closeWebView,
  getUUID,
  scanQRCode,
  startMeeting,
  joinMeeting,
  share,
  copyContent,
  getNetworkType
}



